.main-container {
  position: relative;
  top: 100px;
  width: 100%;
  background-color: var(--background-color);
  display: flex;
  justify-content: center;

  &__restaurant-list {
    display: inline-grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 32px;

    a {
      text-decoration: none;
      cursor: pointer;
    }
  }
}
